@import "./variable.scss";
@import "./default.scss";
.background {
  background-color: $dark-blue2;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  position: relative;
  display: block;
  min-width: 70%;
  min-height: 80vh;
  background-color: $dark-blue1;
}
.container-form {
  height: 100%;
  position: absolute;
  width: 50%;
  transition: all 0.6s ease-in-out;
}
.container-signup {
  //   left: 0;
  //   opacity: 0;
  //   width: 50%;
  //   z-index: 1;
}
.form1 {
  margin: 3vw 5vw;
  color: $white;
}
.form-flex {
  display: flex;
  flex-direction: column;

  margin: 2vw 0;
}
.textbox {
  display: flex;
 background-color:$dark-blue2;
  border-top: 1px solid #e5e4e2;
  border-bottom: 1px solid #e5e4e2;
  border-left: 5px solid $light-blue;
  border-right: 5px solid $light-blue;
  span {
    @include flex-center;

    border-right: 1px solid #e5e4e2;
    i {
      padding: 0 10px;
    }
  }
  div {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    p {
      color: $orange;
      padding: 0.5vw;
      font-size:12px;
      border-bottom:  1px solid #e5e4e2;
    }
    input {
      background-color: transparent;
      border: none;
      height: 2vw;
      color:$white;
      
    }
  }
}
